import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import MenuItem from '@material-ui/core/MenuItem';
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, data1 } = props.table;
        let art = selected!==-1?data[selected][0]:'';
        let address = selected!==-1?data[selected][4]:'';
        let summ = selected!==-1?data[selected][2]+'$':'';
         let [status, setStatus] = useState(selected!==-1?data[selected][5]:'');
        let handleStatus =  (event) => {
            setStatus(event.target.value)
        };
        const statuses = ['', '', '']
        let [text, setText] = useState(selected!==-1?data[selected][6]:'');
        let handleText =  (event) => {
            setText(event.target.value)
        };

        const { classes } = props;
        return (
            <div>
                <b></b>
                <br/>
                {art}
                <br/>
                <br/>
                <b></b>
                <br/>
                {address}
                <br/>
                <br/>
                <b></b>
                <br/>
                <br/>
                {summ}
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label=''
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={status}
                    onChange={handleStatus}
                >
                    {
                        statuses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    multiline
                    rowsMax='4'
                    label=''
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={text}
                    onChange={handleText}
                />
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                         if(selected!==-1)
                            setData({id: data[selected][8], search: '', sort: '', page: 0, name: '', data: {status: status, data: text}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));